'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import Image from 'next/image';
import { useAuth } from '@/contexts/AuthContext';
import { 
  HomeIcon, ChartBarIcon, BriefcaseIcon, DocumentTextIcon, 
  UserIcon, ChatBubbleLeftRightIcon, CreditCardIcon, CogIcon,
  ArrowRightOnRectangleIcon, BellIcon, EnvelopeIcon, BuildingStorefrontIcon
} from '@heroicons/react/24/outline';

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const pathname = usePathname();
  const { user, isLoading, logout } = useAuth();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  
  // التحقق من تسجيل الدخول
  useEffect(() => {
    if (!isLoading && !user) {
      window.location.href = '/login?redirect=dashboard';
    }
  }, [user, isLoading]);
  
  if (isLoading || !user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="w-12 h-12 rounded-full border-4 border-indigo-300 border-t-indigo-600 animate-spin"></div>
      </div>
    );
  }
  
  // قائمة العناصر المشتركة
  const commonNavItems = [
    { name: 'لوحة التحكم', href: '/dashboard', icon: HomeIcon },
    { name: 'الرسائل', href: '/dashboard/messages', icon: EnvelopeIcon, badge: 3 },
    { name: 'الإشعارات', href: '/dashboard/notifications', icon: BellIcon, badge: 5 },
    { name: 'الملف الشخصي', href: '/dashboard/profile', icon: UserIcon },
    { name: 'الإعدادات', href: '/dashboard/settings', icon: CogIcon },
  ];
  
  // قائمة عناصر العميل
  const clientNavItems = [
    { name: 'طلباتي', href: '/dashboard/orders', icon: DocumentTextIcon },
    { name: 'المدفوعات', href: '/dashboard/payments', icon: CreditCardIcon },
    { name: 'المفضلة', href: '/dashboard/favorites', icon: BuildingStorefrontIcon },
  ];
  
  // قائمة عناصر مزود الخدمة
  const providerNavItems = [
    { name: 'خدماتي', href: '/dashboard/services', icon: BuildingStorefrontIcon },
    { name: 'الطلبات الواردة', href: '/dashboard/requests', icon: DocumentTextIcon },
    { name: 'أعمالي', href: '/dashboard/portfolio', icon: BriefcaseIcon },
    { name: 'الإحصائيات', href: '/dashboard/statistics', icon: ChartBarIcon },
    { name: 'المدفوعات', href: '/dashboard/earnings', icon: CreditCardIcon },
  ];
  
  // تحديد القائمة المناسبة حسب نوع المستخدم
  const navItems = [
    ...commonNavItems,
    ...(user.role === 'provider' ? providerNavItems : clientNavItems)
  ];
  
  return (
    <div className="min-h-screen bg-gray-50 flex flex-col md:flex-row">
      {/* شريط التنقل العلوي للجوال */}
      <div className="bg-white shadow-sm border-b border-gray-200 md:hidden">
        <div className="px-4 py-2 flex items-center justify-between">
          <button
            type="button"
            className="text-gray-500 hover:text-gray-600"
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          >
            <svg className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            </svg>
          </button>
          
          <Link href="/" className="flex items-center">
            <Image
              src="/logo.svg"
              alt="عرب AI"
              width={40}
              height={40}
              onError={(e) => {
                const target = e.target as HTMLImageElement;
                target.style.display = 'none';
              }}
            />
            <span className="mr-2 font-semibold text-lg text-indigo-600">عرب AI</span>
          </Link>
          
          <div className="relative">
            <div className="relative h-9 w-9 rounded-full overflow-hidden">
              <Image
                src={user.image || "/images/avatar.jpg"}
                alt={user.name}
                fill
                className="object-cover"
                onError={(e) => {
                  const target = e.target as HTMLImageElement;
                  target.src = "/images/avatar.jpg";
                }}
              />
            </div>
            {user.role === 'provider' && (
              <span className="absolute -top-1 -right-1 block h-3.5 w-3.5 rounded-full bg-green-400 border-2 border-white"></span>
            )}
          </div>
        </div>
      </div>
      
      {/* القائمة الجانبية للجوال */}
      {isMobileMenuOpen && (
        <div className="fixed inset-0 z-40 md:hidden">
          <div className="fixed inset-0 bg-gray-600 bg-opacity-75" onClick={() => setIsMobileMenuOpen(false)}></div>
          
          <div className="relative flex-1 flex flex-col max-w-xs w-full bg-white h-full">
            <div className="px-4 py-4 flex items-center justify-between border-b border-gray-200">
              <div className="flex items-center">
                <Image
                  src="/logo.svg"
                  alt="عرب AI"
                  width={40}
                  height={40}
                  onError={(e) => {
                    const target = e.target as HTMLImageElement;
                    target.style.display = 'none';
                  }}
                />
                <span className="mr-2 font-semibold text-lg text-indigo-600">عرب AI</span>
              </div>
              
              <button
                type="button"
                className="text-gray-400 hover:text-gray-500"
                onClick={() => setIsMobileMenuOpen(false)}
              >
                <svg className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
            
            <div className="pt-5 pb-4 overflow-y-auto h-full">
              <div className="px-4 mb-6">
                <div className="flex items-center">
                  <div className="relative h-12 w-12 rounded-full overflow-hidden">
                    <Image
                      src={user.image || "/images/avatar.jpg"}
                      alt={user.name}
                      fill
                      className="object-cover"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement;
                        target.src = "/images/avatar.jpg";
                      }}
                    />
                  </div>
                  <div className="mr-3">
                    <p className="text-base font-medium text-gray-800">{user.name}</p>
                    <p className="text-sm text-gray-500">{user.email}</p>
                  </div>
                </div>
              </div>
              
              <nav className="mt-2 px-2 space-y-1">
                {navItems.map((item) => {
                  const isActive = pathname === item.href;
                  
                  return (
                    <Link
                      key={item.name}
                      href={item.href}
                      className={`group flex items-center px-3 py-2 text-sm font-medium rounded-md ${
                        isActive
                          ? 'bg-indigo-50 text-indigo-700'
                          : 'text-gray-700 hover:text-gray-900 hover:bg-gray-50'
                      }`}
                      onClick={() => setIsMobileMenuOpen(false)}
                    >
                      <item.icon
                        className={`ml-3 flex-shrink-0 h-6 w-6 ${
                          isActive ? 'text-indigo-600' : 'text-gray-400 group-hover:text-gray-500'
                        }`}
                      />
                      {item.name}
                      {item.badge && (
                        <span
                          className={`mr-auto inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${
                            isActive ? 'bg-indigo-200 text-indigo-800' : 'bg-gray-200 text-gray-800'
                          }`}
                        >
                          {item.badge}
                        </span>
                      )}
                    </Link>
                  );
                })}
                
                <button
                  onClick={() => logout()}
                  className="group flex items-center px-3 py-2 text-sm font-medium text-gray-700 hover:text-gray-900 hover:bg-gray-50 w-full rounded-md mt-4"
                >
                  <ArrowRightOnRectangleIcon className="ml-3 flex-shrink-0 h-6 w-6 text-gray-400 group-hover:text-gray-500" />
                  تسجيل الخروج
                </button>
              </nav>
            </div>
          </div>
        </div>
      )}
      
      {/* القائمة الجانبية للكمبيوتر */}
      <div className="hidden md:flex md:flex-col md:w-64 md:fixed md:inset-y-0">
        <div className="flex-1 flex flex-col min-h-0 bg-white border-l border-gray-200">
          <div className="flex-1 flex flex-col pt-5 pb-4 overflow-y-auto">
            <div className="flex items-center justify-center flex-shrink-0 px-4 mb-6">
              <Link href="/" className="flex items-center">
                <Image
                  src="/logo.svg"
                  alt="عرب AI"
                  width={40}
                  height={40}
                  onError={(e) => {
                    const target = e.target as HTMLImageElement;
                    target.style.display = 'none';
                  }}
                />
                <span className="mr-2 font-semibold text-xl text-indigo-600">عرب AI</span>
              </Link>
            </div>
            
            <div className="px-4 mb-6">
              <div className="flex items-center">
                <div className="relative h-12 w-12 rounded-full overflow-hidden">
                  <Image
                    src={user.image || "/images/avatar.jpg"}
                    alt={user.name}
                    fill
                    className="object-cover"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement;
                      target.src = "/images/avatar.jpg";
                    }}
                  />
                </div>
                <div className="mr-3">
                  <p className="text-sm font-medium text-gray-800">{user.name}</p>
                  <p className="text-xs text-gray-500">{user.email}</p>
                </div>
              </div>
            </div>
            
            <nav className="mt-2 px-2 space-y-1">
              {navItems.map((item) => {
                const isActive = pathname === item.href;
                
                return (
                  <Link
                    key={item.name}
                    href={item.href}
                    className={`group flex items-center px-3 py-2 text-sm font-medium rounded-md ${
                      isActive
                        ? 'bg-indigo-50 text-indigo-700'
                        : 'text-gray-700 hover:text-gray-900 hover:bg-gray-50'
                    }`}
                  >
                    <item.icon
                      className={`ml-3 flex-shrink-0 h-6 w-6 ${
                        isActive ? 'text-indigo-600' : 'text-gray-400 group-hover:text-gray-500'
                      }`}
                    />
                    {item.name}
                    {item.badge && (
                      <span
                        className={`mr-auto inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${
                          isActive ? 'bg-indigo-200 text-indigo-800' : 'bg-gray-200 text-gray-800'
                        }`}
                      >
                        {item.badge}
                      </span>
                    )}
                  </Link>
                );
              })}
              
              <button
                onClick={() => logout()}
                className="group flex items-center px-3 py-2 text-sm font-medium text-gray-700 hover:text-gray-900 hover:bg-gray-50 w-full rounded-md mt-4"
              >
                <ArrowRightOnRectangleIcon className="ml-3 flex-shrink-0 h-6 w-6 text-gray-400 group-hover:text-gray-500" />
                تسجيل الخروج
              </button>
            </nav>
          </div>
        </div>
      </div>
      
      {/* المحتوى الرئيسي */}
      <div className="md:pr-64 flex flex-col flex-1">
        {/* محتوى الصفحة */}
        <main className="flex-1 px-4 py-6 md:px-8 md:py-8">
          {children}
        </main>
      </div>
    </div>
  );
} 